<script>
import dayjs from "dayjs";

export default {
  filters: {
    date(val) {
      return dayjs(val).format("MM/DD");
    },
    date1(val) {
      return dayjs(val).format("MM-DD");
    },
  },
  data() {
    return {
      swiperOption: {
        pagination: {
          el: ".pagination-home",
        },
      },
      navItem: [
        { name: "爆料站", icon: "sprite-news" },
        { name: "故事站", icon: "sprite-story" },
        { name: "周边商城", icon: "sprite-shop" },
        { name: "体验服", icon: "sprite-tiyan" },
        { name: "新人专区", icon: "sprite-newpeople" },
        { name: "荣耀传承", icon: "sprite-inherit" },
        { name: "王者营地", icon: "sprite-camp" },
        { name: "公众号", icon: "sprite-public" },
        { name: "版本介绍", icon: "sprite-edition" },
        { name: "对局环境", icon: "sprite-duiju" },
        { name: "无限王者团", icon: "sprite-wangzhetuan" },
        { name: "创意互动营", icon: "sprite-interaction" },
      ],
      newsCats: [],
      heroCats: [],
      adList: {},
      HeroBanner: {},
      videoCats: [],
    };
  },
  methods: {
    async fetchAds() {
      const res = await this.$http.get("/ads/list/首页轮播");
      this.adList = res.data;
      console.log(this.adList, "首页轮播");
    },
    async fetchBanner() {
      const res = await this.$http.get("/ads/list/HeroBanner");
      this.HeroBanner = res.data.items[0];
      console.log(this.HeroBanner, "新英雄banner");
    },
    async fetchNewsCats() {
      const res = await this.$http.get("news/list");
      this.newsCats = res.data;
      console.log(this.newsCats, "新闻列表");
    },
    async fetchHeroCats() {
      const res = await this.$http.get("heroes/list");
      this.heroCats = res.data;
      console.log(this.heroCats, "英雄列表");
    },
    async fetchVideo() {
      const res = await this.$http.get("video/list");
      this.videoCats = res.data;
      console.log(this.videoCats, "视频列表");
    },
  },
  created() {
    this.fetchNewsCats();
    this.fetchHeroCats();
    this.fetchVideo();
    this.fetchAds();
    this.fetchBanner();
  },
};
</script>

